const player = require("./Player");
const deck = require("./Deck");
const uniqueID = require("unique-string");
const helpers = require("./helpers");

class Game {
  constructor() {
    this.game = {
      gameId: uniqueID(),
      players: [],
      active: true,
      deck: deck
    };
  }
  startGame() {
    this.game.players.push(player);
    //this.game.players.push(player);
    return this.game;
  }
}

module.exports = new Game().startGame();
